<template>
  <div>
    <da-equal-panel :left-width="null" right-width="500px" height="auto">
      <div slot="left">
        <da-panel title="用能总览" width="100%" height="auto" padding-content="t" padding="b" min-height="200px" ref="panelEuovTree">
          <da-equal-panel left-width="75px" height="auto">
            <div slot="left">
              <da-radio-block class="egycs-time-select" v-model="eCAT" :data-lists="eCATList" width="100%" height="104px" label="name" value-key="value" @change="getEgyCspAnalysisData"></da-radio-block>
            </div>
            <div class="clearfix" slot="right">
              <carbon-count width="200px" height="92px" :data-lists="energyTypeData" :attr='{name: "name",value: "dataValue",unit: "targetUnit"}'>
                <template slot-scope="scope">
                  <p class="text-left">{{scope.data.trueName||scope.data.name}}</p>
                  <p class="euover-data">{{scope.data.dataValue||0}}</p>
                  <p class="text-right">
                    <small>{{scope.data.targetUnit||"-"}}</small>
                  </p>
                </template>
              </carbon-count>
              <da-equal-panel left-width="200px" height="auto">
                <div class="egycs-color-block" slot="left">
                  <p>{{timelabel}}</p>
                  <p>能源总成本</p>
                  <p class="egycs-yellow">{{energyTotalCost.dataValue||"-"}}</p>
                  <p>{{energyTotalCost.targetUnit||"-"}}</p>
                </div>
                <div slot="right" style="font-size: 0;">
                  <da-panel :no-head="true" width="50%" padding="tbr" height="auto" min-width="190px">
                    <da-pie-chart id="egyCspAnalysis" class="inblock" title="分类能耗占比分析" :show-title="true" width="100%" height="191px" :data-lists="egyCspAnalysisData" :legend-displace="energyDataObj" label="contentId"></da-pie-chart>
                  </da-panel>
                  <da-panel :no-head="true" width="50%" padding="tbr" height="auto" min-width="190px">
                    <da-pie-chart id="energyCostAnalysis" class="inblock" title="能源成本占比分析" :show-title="true" width="100%" height="191px" :data-lists="energyCostPie" :legend-displace="energyDataObj" label="contentId"></da-pie-chart>
                  </da-panel>
                </div>
              </da-equal-panel>
            </div>
          </da-equal-panel>
        </da-panel>
      </div>
      <div class="clearfix" slot="right">
        <da-panel title="年度节能目标实现情况" width="100%" height="auto" padding="lb" padding-content="tb" ref="panelEuovTarget">

          <da-equal-panel left-width="200px" height="auto">

            <carbon-count slot="left" class="pad-lr-10" width="200px" height="92px" :data-lists="egyTargStatusDataLists" :attr='{name: "name",value: "value",unit: "unit"}'>
              <template slot-scope="scope">
                <p class="text-left">{{scope.data.name}}</p>
                <p class="euover-data">{{scope.data.value||0}}</p>
                <p class="text-right">
                  <small>{{scope.data.unit||"-"}}</small>
                </p>
              </template>
            </carbon-count>
            <!-- <p class="text-center egy-target-status">年度节能目标:{{egyTargStatusData[1]?(egyTargStatusData[1].dataValue.toFixed(egyTargStatusData[1].dataValue.displayPrecisions)+" "+egyTargStatusData[1].targetUnit):"-"}}</p>
          <p class="text-center egy-target-status">本年度节能量:{{egyTargStatusData[0]?(egyTargStatusData[0].dataValue.toFixed(egyTargStatusData[0].dataValue.displayPrecisions)+" "+egyTargStatusData[0].targetUnit):"-"}}</p>
          <p class="text-center egy-target-status">目标达成率：{{saveEnergySacle}}%</p> -->
            <div slot="right">
              <da-line-chart id="egyTargStatus" type="bar" :data-lists="egyTargStatusData" height="296px" x-axis-key="name" label="节能量"></da-line-chart>
            </div>
          </da-equal-panel>
        </da-panel>
      </div>
    </da-equal-panel>

    <da-panel title="能源消耗趋势" width="50%" height="auto" min-height="200px" margin="12px 0 0" padding="rb" ref="panelEuovTrend">
      <da-equal-panel left-width="70px" height="250px">
        <div slot="left">
          <da-radio-block class="egycs-time-select" v-model="eCTT" :is-stack="true" :data-lists="eCTTList" width="100%" label="name" value-key="value" @change="getEgyCspTrendData"></da-radio-block>
        </div>
        <div slot="right">
          <da-line-chart id="egyCspTrend" type="bar" :date-time="eCTT" :data-lists="egyCspTrendData" :legend-displace="energyDataObj" label="contentId" :ny-axis="2" :format="eCTTFormat" :x-axis-format="eCTTFormat"></da-line-chart>
        </div>
      </da-equal-panel>
    </da-panel>

    <da-panel title="单耗趋势分析" width="50%" height="auto" min-height="200px" padding="b" ref="panelEuov">

      <div slot="search">
        <da-radio-select v-model="products" :maxNum="1" :option-lists="productList" label="name" value-key="id" @change="getSglTrendAnalysisData"></da-radio-select>
        <da-radio-select v-model="energys" :maxNum="1" :option-lists="energyList" label="name" value-key="id" @change="getSglTrendAnalysisData"></da-radio-select>
      </div>
      <da-equal-panel left-width="70px" height="250px">
        <div slot="left">
          <da-radio-block class="egycs-time-select" v-model="sTAT" :data-lists="eCTTList" width="100%" label="name" value-key="value" @change="getSglTrendAnalysisData"></da-radio-block>
        </div>
        <div slot="right">
          <da-line-chart id="sglTrendAnalysis" :date-time="sTAT" :data-lists="sglTrendAnalysisData" label="productId" :format="sTATFormat" :x-axis-format="sTATFormat" :legend-displace="productObj"></da-line-chart>
        </div>
      </da-equal-panel>
    </da-panel>
    <da-panel title="设备能耗排名" width="50%" height="auto" min-height="200px" padding="rb" ref="panelCount">
      <da-equal-panel left-width="70px" height="250px">
        <div slot="left">
          <da-radio-block class="egycs-time-select" v-model="eECT" :data-lists="eCATList" width="100%" label="name" value-key="value" @change="getEquipmentEgyCspData"></da-radio-block>
        </div>
        <div slot="right">
          <da-line-chart id="equipmentEgyCsp" type="bar" :data-lists="equipmentEgyCspData" label="电" x-axis-key="trueName"></da-line-chart>
        </div>
      </da-equal-panel>
    </da-panel>
    <da-panel title="分项能耗占比" width="50%" height="auto" min-height="200px" padding="b" ref="panelCount">
      <da-equal-panel left-width="70px" height="250px">
        <div slot="left">
          <da-radio-block class="egycs-time-select" v-model="sRT" :data-lists="eCATList" width="100%" label="name" value-key="value" @change="getSubitemRatioData"></da-radio-block>
        </div>
        <div slot="right">
          <da-pie-chart id="subitemRatio" title="分项能耗占比" :data-lists="subitemRatioData" label="energySubitemId" value-key="displayData" unit="displayUnit" :legend-displace="energySubItems"></da-pie-chart>
        </div>
      </da-equal-panel>
    </da-panel>
  </div>
</template>

<script>
import {
  reduceById,
  calcTime,
  cycleTime,
  deepJSONCopy,
  changeToTree,
  extend,
  changeArr,
  getFixedDisplayData
} from "../../../util/util";
import carbonCount from "../analysis/components/carbonData";
export default {
  components: {
    carbonCount
  },
  data() {
    let targetConstant = this.$store.state.targetConstant;
    let time = new Date();
    let date = this.$moment(time, "YYYY-MM-DD");
    let toDay = date + "," + date + "," + targetConstant.targetCycle.day;
    let toMonth =
      cycleTime(time, 5).join(",") + "," + targetConstant.targetCycle.month;
    let toYear =
      cycleTime(time, 8).join(",") + "," + targetConstant.targetCycle.year;

    let sTimeDay = calcTime(time, -15, 3, "YYYY-MM-DD");
    let eTimeDay = calcTime(time, -1, 3, "YYYY-MM-DD");
    let day = sTimeDay + "," + eTimeDay + "," + targetConstant.targetCycle.day;

    let sTimeMonth = calcTime(time, -6, 5, "YYYY-MM-01");
    let eTimeMonth = calcTime(
      this.$moment(time, "YYYY-MM-01"),
      -1,
      3,
      "YYYY-MM-DD"
    );
    let month =
      sTimeMonth + "," + eTimeMonth + "," + targetConstant.targetCycle.month;

    let sTimeYear = calcTime(time, -6, 8, "YYYY-01-01");
    let eTimeYear = calcTime(time, -1, 8, "YYYY-12-31");
    let year =
      sTimeYear + "," + eTimeYear + "," + targetConstant.targetCycle.year;
    return {
      // 默认参数
      productObj: {},
      orgId: targetConstant.orgId.enterprise,
      cycle: [
        targetConstant.targetCycle.year,
        targetConstant.targetCycle.month,
        targetConstant.targetCycle.day
      ],
      targetType: targetConstant.targetType,
      contentTypes: targetConstant.targetContentType.energy,

      energySubItems: {}, // 用能分项Object
      // 能源ID，逗号隔开
      allEnergyIds: "",
      // 能源种类
      energyData: [],
      energyDataObj: {},
      // 用能总览-获取分类能源能耗
      energyTypeData: [],
      // 用能总览-本月能源总成本
      energyTotalCost: {},
      // 用能总览-本月能源成本（除综合能耗）
      energyCostPie: [],
      // 绑定年月日
      eCAT: toYear,
      eCATList: [
        {
          name: "本日",
          value: toDay
        },
        {
          name: "本月",
          value: toMonth
        },
        { name: "本年", value: toYear }
      ],
      // 分类能耗占比分析数据
      egyCspAnalysisData: [],
      // 年度节能目标实现情况时间
      egyTargStatusTime: ["目标节能量", "实际节能量"],
      // 年度节能目标实现情况数据
      egyTargStatusData: [],
      // 实际节能量达成率
      saveEnergySacle: "",
      // 绑定年月日
      eCTT: month,
      eCTTList: [
        { name: "日", value: day },
        { name: "月", value: month },
        { name: "年", value: year }
      ],
      // 能源消耗趋势时间
      egyCspTrendTime: "",
      // 能源消耗趋势数据
      egyCspTrendData: [],
      // 绑定年月日
      sTAT: month,
      // 单耗趋势分析时间
      sglTrendAnalysisTime: "",

      // 单耗趋势功能选择的产品
      products: "",
      // 单耗趋势功能查询的产品
      productList: [],
      // 单耗趋势功能选择的能源
      energys: targetConstant.energy.compreEnergyConsum,
      // 单耗趋势功能查询的能源
      energyList: [],
      // 单耗趋势分析数据
      sglTrendAnalysisData: [],
      // 绑定年月日
      eECT: toYear,
      // 设备能耗排名数据
      equipmentEgyCspTime: "",
      // 设备能耗排名数据
      equipmentEgyCspData: [],
      // 绑定年月日
      sRT: toYear,
      // 分项能耗占比
      subitemRatioData: []
    };
  },
  computed: {
    timelabel() {
      return this.eCATList.filter(item => {
        return item.value === this.eCAT;
      })[0].name;
    },
    eCTTFormat() {
      return this.getFormatByType(this.eCTT.split(",")[2]);
    },
    sTATFormat() {
      return this.getFormatByType(this.sTAT.split(",")[2]);
    },
    eECTFormat() {
      return this.getFormatByType(this.eECT.split(",")[2]);
    },
    egyTargStatusDataLists() {
      let data = [
        {
          name: "本年度目标节能量",
          value: this.egyTargStatusData[0]
            ? this.egyTargStatusData[0].dataValue.toFixed(
                this.egyTargStatusData[0].dataValue.displayPrecisions
              )
            : "-",
          unit: this.egyTargStatusData[0]
            ? this.egyTargStatusData[0].targetUnit
            : "-"
        },
        {
          name: "本年度实际节能量",
          value: this.egyTargStatusData[1]
            ? this.egyTargStatusData[1].dataValue.toFixed(
                this.egyTargStatusData[1].dataValue.displayPrecisions
              )
            : "-",
          unit: this.egyTargStatusData[1]
            ? this.egyTargStatusData[1].targetUnit
            : "-"
        },
        {
          name: "节能目标完成率",
          value: this.saveEnergySacle,
          unit: "%"
        }
      ];
      return data;
    }
  },
  methods: {
    getFormatByType(type) {
      let targetCycle = this.$store.state.targetConstant.targetCycle;
      let format = "YYYY-MM-DD";
      switch (parseInt(type)) {
        case targetCycle.year:
          format = "YYYY";
          break;
        case targetCycle.month:
          format = "YYYY-MM";
          break;
        case targetCycle.day:
          format = "YYYY-MM-DD";
          break;
      }
      return format;
    },
    inintData() {
      this.$ajax
        .all([this.getEnergyData(), this.getProductInfo()])
        .then(res => {
          if (res[0].data.status) {
            this.energyData = res[0].data.records;
            this.energyList = res[0].data.records;
            this.energyData.forEach(item => {
              if (this.allEnergyIds === "") {
                this.allEnergyIds += item.id;
              } else {
                this.allEnergyIds += "," + item.id;
              }
              this.energyDataObj[item.id] = item.name;
            });
          }
          if (res[1].data.status) {
            this.products = res[1].data.records[0].id;
            this.productList = res[1].data.records;
            let data = res[1].data.records;
            data.forEach(item => {
              this.productObj[item.id] = item.name;
            });
          }
          this.$nextTick(() => {
            this.loadData();
          });
        });
    },
    loadData() {
      // // 用能总览 - 获取分类能源能耗;
      // this.getEnergyTypeData();
      // // 用能总览-本月能源总成本
      // this.getEnergyTotalCost();
      // 用能总览 - 分类能耗占比分析;
      this.getEgyCspAnalysisData();

      // 年度节能目标实现情况数据
      this.getEgyTargStatusData();
      // 能源消耗趋势
      this.getEgyCspTrendData();
      // 单耗趋势分析
      this.getSglTrendAnalysisData();
      // 设备能耗排名(前10);
      this.getEquipmentEgyCspData();
      // 分项能耗占比
      this.getSubitemItemAndData();
    },
    /**
     * 查询产品信息
     */
    getProductInfo() {
      return this.$ajax({
        url: "rest/analysisData/product",
        method: "get",
        params: { title: "产品信息", orgIds: this.orgId }
      });
    },
    /**
     * 查询用能分项信息
     */
    getEnergySubItems() {
      return this.$ajax({
        url: "rest/analysisData/energySubItem",
        method: "get",
        params: { title: "用能分项" }
      });
    },
    /**
     * 查询能源种类
     */
    getEnergyData() {
      let me = this;
      this.allEnergyIds = "";
      return me.$ajax({
        url: "rest/analysisData/energy",
        method: "get",
        params: { title: "能源种类" }
      });
    },
    // // 用能总览-获取分类能源能耗
    // getEnergyTypeData(orgId, cycle, energyIds, startTime, endTime) {
    //   let me = this;
    //   let toYear = this.eCATList[2].value.split(",");
    //   me
    //     .$ajax({
    //       url: "rest/analysisData/targetData",
    //       method: "get",
    //       params: {
    //         title: "用能总览-获取分类能源能耗", // String,功能标题
    //         orgIds: this.orgId, // String,部门ID
    //         targetCycle: this.cycle[0], // String,指标周期
    //         targetTypes: this.targetType.energyConsum, // String,指标类型
    //         contentTypes: this.contentTypes, // String,统计内容类型
    //         contentIds: this.allEnergyIds, // String,统计内容ID
    //         objTypes: null, // String,指标关联对象类型
    //         objTypeValues: null, // String,指标关联对象值
    //         isQueryAll: false, // Boolean,是否所有指标数据
    //         startTime: toYear[0], // String,开始时间
    //         endTime: toYear[1], // String,结束时间
    //         isOrgId: false, // Boolean,是否返回部门ID
    //         isContentType: true, // Boolean,是否返回指标统计内容类型ID
    //         isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
    //       }
    //     })
    //     .then(res => {
    //       if (res.data.status) {
    //         me.energyTypeData = this.getShowData(res.data.records).map(item => {
    //           let splitName = item.name.split("[")[2].split("]")[0];
    //           item.trueName = splitName;
    //           return item;
    //         });
    //       }
    //     });
    // },
    // /**
    //  * 用能总览-本月能源总成本
    //  * 也就是查综合能耗成本
    //  */
    // getEnergyTotalCost() {
    //   let me = this;
    //   let energyIds = this.$store.state.targetConstant.energy
    //     .compreEnergyConsum;
    //   let toMonth = this.eCATList[1].value.split(",");
    //   me
    //     .$ajax({
    //       url: "rest/analysisData/targetData",
    //       method: "get",
    //       params: {
    //         title: "用能总览-本月能源总成本", // String,功能标题
    //         orgIds: this.orgId, // String,部门ID
    //         targetCycle: this.cycle[1], // String,指标周期
    //         targetTypes: this.targetType.energyCost, // String,指标类型
    //         contentTypes: this.contentTypes, // String,统计内容类型
    //         contentIds: energyIds, // String,统计内容ID
    //         objTypes: null, // String,指标关联对象类型
    //         objTypeValues: null, // String,指标关联对象值
    //         isQueryAll: false, // Boolean,是否所有指标数据
    //         startTime: toMonth[0], // String,开始时间
    //         endTime: toMonth[1], // String,结束时间
    //         isOrgId: false, // Boolean,是否返回部门ID
    //         isContentType: true, // Boolean,是否返回指标统计内容类型ID
    //         isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
    //       }
    //     })
    //     .then(res => {
    //       if (res.data.status) {
    //         me.energyTotalCost =
    //           this.getShowData(
    //             res.data.records,
    //             "dataValue",
    //             "displayPrecisions"
    //           )[0] || {};
    //       }
    //     });
    // },
    getShowData(array, dataField, displayField) {
      if (!dataField || dataField == "") {
        dataField = "dataValue";
      }
      if (!displayField || displayField == "") {
        displayField = "displayPrecisions";
      }
      if (array instanceof Array) {
        for (let i = 0; i < array.length; i++) {
          if (!isNaN(array[i][dataField])) {
            array[i][dataField] = parseFloat(array[i][dataField]);
          }
          if (displayField instanceof Number) {
            array[i][dataField] = array[i][dataField].toFixed(displayField);
          } else {
            array[i][dataField] = array[i][dataField].toFixed(
              array[i][displayField]
            )
              ? array[i][dataField].toFixed(array[i][displayField])
              : array[i][dataField];
          }
        }
      } else if (array instanceof Object) {
        array[dataField] = array[dataField].toFixed(array[displayField]);
      } else if (array instanceof Number) {
        if (dataField instanceof Number) {
          array = array.toFixed(dataField);
        } else if (displayField instanceof Number) {
          array = array.toFixed(displayField);
        } else {
          array = array.toFixed(2);
        }
      }
      return array;
    },
    /**
     * 用能总览-分类能耗占比分析数据
     */
    getEgyCspAnalysisData() {
      let me = this;
      let time = this.eCAT.split(",");
      let targetTypes =
        this.targetType.energyConsum + "," + this.targetType.energyCost;
      let contentTypes = this.contentTypes + "," + this.contentTypes;
      let energyIds = this.allEnergyIds + "#" + this.allEnergyIds;
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: {
            title: "用能总览-分类能耗占比分析", // String,功能标题
            orgIds: this.orgId, // String,部门ID
            targetCycle: time[2], // String,指标周期
            targetTypes: targetTypes, // String,指标类型
            contentTypes: contentTypes, // String,统计内容类型
            contentIds: energyIds, // String,统计内容ID
            objTypes: null, // String,指标关联对象类型
            objTypeValues: null, // String,指标关联对象值
            isQueryAll: false, // Boolean,是否所有指标数据
            startTime: time[0], // String,开始时间
            endTime: time[1], // String,结束时间
            isOrgId: false, // Boolean,是否返回部门ID
            isContentType: true, // Boolean,是否返回指标统计内容类型ID
            isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
          }
        })
        .then(res => {
          this.energyTotalCost = {};
          this.energyCostPie = [];
          if (res.data.status) {
            let data = this.getShowData(res.data.records);
            let energyTypeDataIndex = this.energyData.map(item => {
              return item.id;
            });
            let energyTypeData = deepJSONCopy(this.energyData);
            let obj2 = [];
            let costList = [];
            data.forEach(item => {
              if (item.queryTargetType == this.targetType.energyConsum) {
                if (
                  item.contentId !=
                  this.$store.state.targetConstant.energy.compreEnergyConsum
                ) {
                  obj2.push(item);
                }

                let index = energyTypeDataIndex.indexOf(item.contentId);

                item.trueName = energyTypeData[index].name;
                energyTypeData[index] = item;
              } else if (item.queryTargetType == this.targetType.energyCost) {
                let totalCost = item;
                totalCost.dataValue = (totalCost.dataValue / 10000).toFixed(2);
                totalCost.targetUnit = "万元";
                if (
                  item.contentId ==
                  this.$store.state.targetConstant.energy.compreEnergyConsum
                ) {
                  this.energyTotalCost = totalCost;
                } else {
                  costList.push(totalCost);
                }
              }
            });
            me.egyCspAnalysisData = obj2;
            me.energyTypeData = energyTypeData;
            this.energyCostPie = costList;
          }
        });
    },
    /**
     * 年度节能目标实现情况数据
     */
    getEgyTargStatusData() {
      this.egyTargStatusData = [];

      let energyIds = this.$store.state.targetConstant.energy
        .compreEnergyConsum;
      let toYear = this.eCATList[2].value.split(",");

      this.$ajax
        .all([
          this.getYearSaveEnergy(energyIds, toYear[0], toYear[1], toYear[2]),
          this.getYearSaveEnergyTarget(
            energyIds,
            toYear[0],
            toYear[1],
            toYear[2]
          )
        ])
        .then(res => {
          let obj = [];
          if (
            res[1].data.status &&
            res[1].data.records.target &&
            res[1].data.records.target.length > 0
          ) {
            obj.push({
              id: 1,
              name: "目标节能量",
              dataValue: Number(res[1].data.records.target[0].value.toFixed(2)),
              targetUnit: res[1].data.records.target[0].targetUnit,
              displayPrecisions: res[1].data.records.target[0].displayPrecisions
            });
          }
          if (res[0].data.status) {
            obj.push({
              id: 1,
              name: "实际节能量",
              dataValue: Number(res[0].data.records[0].dataValue.toFixed(2)),
              targetUnit: res[0].data.records[0].targetUnit,
              displayPrecisions: res[0].data.records[0].displayPrecisions
            });
          }
          this.egyTargStatusData = obj;
          //getFixedDisplayData(this.egyTargStatusData);
          this.$nextTick(() => {
            // 目标达成率
            let sacle = "";
            if (this.egyTargStatusData[0] && this.egyTargStatusData[1]) {
              sacle =
                this.egyTargStatusData[0].dataValue != 0
                  ? (
                      this.egyTargStatusData[1].dataValue /
                      this.egyTargStatusData[0].dataValue *
                      100
                    ).toFixed(2)
                  : "--";
            } else {
              sacle = "0";
            }
            this.saveEnergySacle = sacle;
          });
        });
    },
    /**
     * 年度节能目标实现情况--本年度节能目标
     */
    getYearSaveEnergyTarget(energyIds, startTime, endTime, cycle) {
      let me = this;
      let targetType = this.$store.state.targetConstant.targetType.saveEnergy;
      return me.$ajax({
        url: "rest/analysisData/targetBenchmarkData",
        method: "get",
        params: {
          title: "年度节能目标实现情况--本年度节能目标", // String,功能标题
          orgIds: this.orgId, // String,部门ID
          targetCycle: cycle, // String,指标周期
          targetTypes: this.targetType.saveEnergy, // String,指标类型
          contentTypes: this.contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          benchmarkQuota: "001", //
          startTime: startTime, // String,开始时间
          endTime: endTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: false, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    /**
     * 年度节能目标实现情况--本年度节能量
     */
    getYearSaveEnergy(energyIds, startTime, endTime, cycle) {
      let me = this;
      return me.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "年度节能目标实现情况--本年度节能量", // String,功能标题
          orgIds: this.orgId, // String,部门ID
          targetCycle: cycle, // String,指标周期
          targetTypes: this.targetType.saveEnergy, // String,指标类型
          contentTypes: this.contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: startTime, // String,开始时间
          endTime: endTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: false, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    /**
     * 能源消耗趋势
     */
    getEgyCspTrendData() {
      this.egyCspTrendData = [];
      this.$ajax
        .all([this.getEnergyRate(), this.getEnergyDataTrend()])
        .then(res => {
          let rstData = [];
          let energyRateArr = [];
          if (res[0].data.status) {
            energyRateArr = res[0].data.records;
          }
          let dataArr = [];
          if (res[1].data.status) {
            dataArr = this.getShowData(res[1].data.records);
          }
          dataArr.forEach(obj2 => {
            let rate = 1;
            let coefficient = 1;
            let energyId = obj2.contentId;
            if (energyId == this.$store.state.targetConstant.energy.electric) {
              rate = 10000;
              coefficient = 1.229;
            }
            energyRateArr.forEach(obj1 => {
              if (energyId == obj1.contentId) {
                coefficient = obj1.dataValue;
              }
            });
            obj2.showData = obj2.dataValue;
            obj2.showDataUnit = obj2.targetUnit;
            obj2.dataValue = obj2.dataValue / rate * coefficient;
            obj2.targetUnit = "tce";
            rstData.push(obj2);
          });
          this.egyCspTrendData = this.getShowData(rstData);
        });
    },
    /**
     * 能源消耗趋势-折标系数
     */
    getEnergyRate() {
      let me = this;
      let time = this.eCTT.split(",");
      let energyIds = "," + this.allEnergyIds + ",";
      let compreEnergy =
        "," + this.$store.state.targetConstant.energy.compreEnergyConsum + ",";
      energyIds = energyIds.replace(compreEnergy, ",");
      energyIds = energyIds.substring(1);
      energyIds = energyIds.substring(0, energyIds.length - 1);
      let startTime = time[0];
      let endTime = time[1];
      if (
        time[2] == this.$store.state.targetConstant.targetCycle.month ||
        time[2] == this.$store.state.targetConstant.targetCycle.month + ""
      ) {
        let year = time[1].split("-")[0];
        let month = parseInt(time[1].split("-")[1]);
        startTime = year + (month < 9 ? "0" + month : month) + "-01";
        endTime = year + (month < 9 ? "0" + month : month) + "-01";
      } else if (
        time[2] == this.$store.state.targetConstant.targetCycle.year ||
        time[2] == this.$store.state.targetConstant.targetCycle.year + ""
      ) {
        let year = time[1].split("-")[0];
        startTime = year + "-01-01";
        endTime = year + "-01-01";
      }
      let targetType = this.$store.state.targetConstant.targetType.energyRate;
      return me.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "能源消耗趋势——折标系数", // String,功能标题
          orgIds: this.orgId, // String,部门ID
          targetCycle: 3, // String,指标周期
          targetTypes: targetType, // String,指标类型
          contentTypes: this.contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: startTime, // String,开始时间
          endTime: endTime, // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
    },
    /**
     * 能源消耗趋势-能耗实物量
     */
    getEnergyDataTrend() {
      let me = this;
      let time = this.eCTT.split(",");
      let energyIds = "," + this.allEnergyIds + ",";
      let compreEnergy =
        "," + this.$store.state.targetConstant.energy.compreEnergyConsum + ",";
      energyIds = energyIds.replace(compreEnergy, ",");
      energyIds = energyIds.substring(1);
      return me.$ajax({
        url: "rest/analysisData/targetData",
        method: "get",
        params: {
          title: "能源消耗趋势-能耗实物量", // String,功能标题
          orgIds: this.orgId, // String,部门ID
          targetCycle: time[2], // String,指标周期
          targetTypes: this.targetType.energyConsum, // String,指标类型
          contentTypes: this.contentTypes, // String,统计内容类型
          contentIds: energyIds, // String,统计内容ID
          objTypes: null, // String,指标关联对象类型
          objTypeValues: null, // String,指标关联对象值
          isQueryAll: false, // Boolean,是否所有指标数据
          startTime: time[0], // String,开始时间
          endTime: time[1], // String,结束时间
          isOrgId: false, // Boolean,是否返回部门ID
          isContentType: true, // Boolean,是否返回指标统计内容类型ID
          isObjType: null // String,是否返回指标关联对象,1为班组，2为设备，3为产品
        }
      });
      // .then(res => {
      //   if (res.data.status) {
      //     me.egyCspTrendData = this.getShowData(res.data.records);
      //   }
      // });
    },
    /**
     * 单耗趋势分析
     */
    getSglTrendAnalysisData() {
      let me = this;
      let objTypes = this.$store.state.targetConstant.targetSubordinate.product;
      let time = this.sTAT.split(",");
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: {
            title: "单耗趋势分析", // String,功能标题
            orgIds: this.orgId, // String,部门ID
            targetCycle: time[2], // String,指标周期
            targetTypes: this.targetType.unitProductEnergyConsum, // String,指标类型
            contentTypes: this.contentTypes, // String,统计内容类型
            contentIds: this.energys, // String,统计内容ID
            objTypes: objTypes, // String,指标关联对象类型
            objTypeValues: this.products, // String,指标关联对象值
            isQueryAll: false, // Boolean,是否所有指标数据
            startTime: time[0], // String,开始时间
            endTime: time[1], // String,结束时间
            isOrgId: false, // Boolean,是否返回部门ID
            isContentType: false, // Boolean,是否返回指标统计内容类型ID
            isObjType: objTypes // String,是否返回指标关联对象,1为班组，2为设备，3为产品
          }
        })
        .then(res => {
          if (res.data.status) {
            me.sglTrendAnalysisData = this.getShowData(res.data.records);
          }
        });
    },
    /**
     * 设备能耗排名(前10)
     */
    getEquipmentEgyCspData(cycle, startTime, endTime) {
      let me = this;
      let objTypes = this.$store.state.targetConstant.targetSubordinate.equip;
      let energyIds = this.$store.state.targetConstant.energy.electric;
      let time = this.eECT.split(",");
      me
        .$ajax({
          url: "rest/analysisData/targetData",
          method: "get",
          params: {
            title: "设备能耗排名", // String,功能标题
            orgIds: null, // String,部门ID
            targetCycle: time[2], // String,指标周期
            targetTypes: this.targetType.energyConsum, // String,指标类型
            contentTypes: this.contentTypes, // String,统计内容类型
            contentIds: energyIds, // String,统计内容ID
            objTypes: objTypes, // String,指标关联对象类型
            objTypeValues: null, // String,指标关联对象值
            isQueryAll: false, // Boolean,是否所有指标数据
            startTime: time[0], // String,开始时间
            endTime: time[1], // String,结束时间
            isOrgId: false, // Boolean,是否返回部门ID
            isContentType: false, // Boolean,是否返回指标统计内容类型ID
            isObjType: objTypes // String,是否返回指标关联对象,1为班组，2为设备，3为产品
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = res.data.records;
            data = this.sortByKey(data, "dataValue");
            data = this.getShowData(data).map(item => {
              item.trueName = item.name.split("[")[2].split("]")[0];
              return item;
            });
            me.equipmentEgyCspData = data;
          }
        });
    },
    sortByKey(array, key, asc) {
      return array.sort(function(a, b) {
        let x = a[key];
        let y = b[key];
        if (!isNaN(Number(x)) && !isNaN(Number(y))) {
          x = Number(x);
          y = Number(y);
        }
        if (!asc) {
          return x < y ? 1 : x > y ? -1 : 0;
        } else {
          return x < y ? -1 : x > y ? 1 : 0;
        }
      });
    },
    /**
     * 查询用能分项信息和用能分项能耗
     */
    getSubitemItemAndData() {
      this.$ajax.all([this.getEnergySubItems()]).then(res => {
        if (res[0].data.status) {
          let data = res[0].data.records;
          for (let i = 0; i < data.length; i++) {
            this.energySubItems[data[i].id] = data[i].name;
          }
        }
        this.$nextTick(() => {
          this.getSubitemRatioData();
        });
      });
    },
    /**
     * 分项能耗占比
     */
    getSubitemRatioData() {
      let me = this;
      let time = this.sRT.split(",");
      me
        .$ajax({
          url: "rest/analysisData/energySubItemData",
          method: "get",
          params: {
            title: "分项能耗占比", // String,功能标题
            orgIds: null, // String,部门ID
            cycle: time[2], // Integer,周期
            energyIds: null, // String,能源ID
            energySubItems: null, // String,用能分项ID
            startTime: time[0], // String,开始时间
            endTime: time[1], // String,结束时间
            isCount: false, // Boolean,是否统计，可为空，为空表示不统计
            groupBys: "00" // String,按什么统计或是否显示，不可为空，isCount=true表示既统计又显示，isCount=false表示只显示，11（[部门][能源]）
          }
        })
        .then(res => {
          if (res.data.status) {
            me.subitemRatioData = this.getShowData(
              res.data.records,
              "displayData",
              2
            );
          }
        });
    }
  },
  mounted() {
    this.inintData();
  }
};
</script>
<style>
.egycs-yellow {
  font-size: 25px;
  color: yellow;
}
.egycs-color-block {
  display: inline-block;
  width: 190px;
  background-color: #74a9db;
  padding: 43px 0px;
  text-align: center;
  margin: 10px 0;
  color: #fff;
  font-size: 18px;
  line-height: 1.5em;
}

.egycs-time-select {
  display: inline-block;
  width: 75px;
  padding: 40px 5px 0 10px;
}
.egy-target-status {
  font-size: 18px;
  line-height: 1.5em;
}
.euover-data {
  font-size: 24px;
  font-weight: bold;
  padding: px 0px;
  text-align: center;
  color: #ebc85e;
}
</style>


